<template>
  <div class="container-board">
    <div class="columns">
      <!-- Todo Column -->
      <div class="column col-5">
        <div class="panel">
          <div class="panel-header">
            <div class="panel-title">Todo</div>
          </div>
          <div class="panel-body">
            <todo
              v-for="todo in uncheckeds"
              :key="todo.id"
              :todo="todo"
              @remove="removeTodo"
              @toggle="toggleTodo"
            />
          </div>
          <div class="panel-footer text-right">
            <button
              v-if="uncheckeds.length > 0"
              @click="checkAll"
              class="btn btn-link"
            >Concluir Tudo</button>
          </div>
        </div>
      </div>

      <!-- Done Column -->
      <div class="column col-5">
        <div class="panel">
          <div class="panel-header">
            <div class="panel-title">Done</div>
          </div>
          <div class="panel-body">
            <todo
              v-for="todo in checkeds"
              :key="todo.id"
              :todo="todo"
              @remove="removeTodo"
              @toggle="toggleTodo"
            />
          </div>
          <div class="panel-footer text-right">
            <button
              v-if="checkeds.length > 0"
              @click="uncheckAll"
              class="btn btn-link"
            >Desmarcar Tudo</button>

            <button
              v-if="checkeds.length > 0"
              @click="removeAllCheckeds"
              class="btn btn-link text-error"
            >Remover Tudo</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Todo from "@/components/Todo";
import { mapGetters, mapActions } from "vuex";
export default {
  components: { Todo },
  computed: {
    ...mapGetters(["uncheckeds", "checkeds"])
  },
  methods: {
    ...mapActions(["toggleTodo", "removeTodo", "checkAll", "uncheckAll", 'removeAllCheckeds'])
  }
};
</script>

<style scoped>
.container-board {
  height: calc(100vh - 100px);
  padding: 10px;
}

.columns,
.columns .column,
.panel {
  height: 100%;
}

.columns .column .panel {
  border: 0;
  box-shadow: 0 0.25rem 1rem rgba(48, 55, 66, 0.15);
}
</style>